import React from "react"
import { Link } from "gatsby"

const Folders = ({ folders, location, prevTitle }) => {
  const underThree = folders.length < 3
  return (
    <ol
      className="folder-list"
      style={underThree ? { justifyContent: "start" } : null}
    >
      {folders.map(f => {
        return (
          <li
            key={f.name}
            style={underThree ? { marginRight: "var(--spacing-2)" } : null}
          >
            <Link
              key={f.name}
              to={f.path}
              itemProp="url"
              state={{ prevPath: location.pathname, prevTitle }}
            >
              <span>{f.name}</span>
            </Link>
          </li>
        )
      })}
    </ol>
  )
}

export default Folders
